body {
  &.dark__theme {
    background-color: #303030;
  }

  &.dark__theme .up-btn {
    fill: $white-color;
  }
}

.hidden-section__title {
  @include center;
  @extend %visually-hidden;
}

.not-logged-gallery, .empty-lib-gallery {
  display: flex;
  align-items: center;
  justify-content: center;
  color: $black-color;
  
  @include mq(tablet) {
    font-size: 38px;
  }

  body.dark__theme & {
    color: $white-color;
  }
}

.gallery-section {
  position: relative;
}

.icon__button {
  position: absolute;
  background-color: #ff6b01;

  top: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;

  opacity: 0.7;

  display: flex;
  align-items: center;
  justify-content: center;
  transition: $typical-transition-opacity;

  &:hover {
    opacity: 1;
  }

  @include mq(tablet) {
    right: 3%;
    width: 40px;
    height: 40px;
  }
}
.icon__button--opacity {
  opacity: 0.33;
}

.icon__item--hidden {
  display: none;
}

.container--space {
  padding-top: 20px;
  padding-bottom: 40px;

  @include mq(tablet) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  row-gap: 20px;

  @media screen and (min-width: 320px) {
    width: 100%;
  }

  @media screen and (min-width: 768px) {
    gap: 32px;
  }

  @media screen and (min-width: 1280px) {
    column-gap: 16px;
  }
}

.card {
  width: 100%;

  display: flex;
  flex-direction: column;
  border-radius: 5px;
  transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(1.03);
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    body.dark__theme & {
      box-shadow: 0 14px 28px rgba(255, 255, 255, 0.25),
        0 10px 10px rgba(255, 255, 255, 0.22);
    }
  }
  @media screen and (min-width: 320px) {
    min-height: 438px;
  }

  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 32px) / 2);
    min-height: 495px;
  }

  @media screen and (min-width: 1280px) {
    flex-basis: calc((100% - 16px * 2) / 3);
    min-height: 634px;
  }
}
.card__img {
  margin-bottom: 8px;

  @media screen and (min-width: 320px) {
    height: 398px;
  }

  @media screen and (min-width: 768px) {
    height: 455px;
  }

  @media screen and (min-width: 1280px) {
    height: 574px;
  }

  body.dark__theme & {
    box-shadow: 0 14px 28px rgba(255, 255, 255, 0.25),
      0 10px 10px rgba(255, 255, 255, 0.22);
  }
}

.card__wrapper {
  flex-grow: 1;
  padding-left: 10px;
  padding-bottom: 10px;
}

.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  @include mq(desktop) {
    font-size: 18;
  }
}

.card__description {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  @include mq(desktop) {
    font-size: 16;
  }
}

.card__name {
  color: $black-color;

  body.dark__theme & {
    color: $white-color;
  }
}

.card__description {
  color: $orange-color;
}

.card__vote {
  padding: 2px 10px;
  margin-left: 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  border-radius: 5px;
  color: $white-color;
  background-color: $orange-color;

  @include mq(desktop) {
    font-size: 16;
  }
}
